import * as GWU from 'gw-utils';
import { Actor } from '../actor';
import { Scent } from './scent';
// import { Memory } from '../memory';
export class Player extends Actor {
    constructor(kind) {
        super(kind);
        this.scent = new Scent(this);
    }
    interrupt() {
        this.clearGoal();
    }
    endTurn(pct = 100) {
        if (this.map) {
            this.map.fov.update();
            this.scent.update();
        }
        return super.endTurn(pct);
    }
    addToMap(map, x, y) {
        if (!super.addToMap(map, x, y))
            return false;
        this.scent.clear();
        return true;
    }
    pathTo(...args) {
        let x = args[0];
        let y = args[1];
        if (args.length === 1) {
            x = args[0].x;
            y = args[0].y;
        }
        if (!this.map)
            return null;
        const mapToPlayer = this.mapToMe();
        const path = GWU.path.getPath(mapToPlayer, x, y, (x, y) => !this.map.fov.isRevealed(x, y));
        return path;
    }
}
Player.default = {
    ch: '@',
    fg: 'white',
    name: 'You',
    swim: true,
};
